/* src/app/features/home/home-page.component.css */
.hero-section {
  position: relative;
  min-height: 92vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background:
    linear-gradient(
      135deg,
      #0B6EFF 0%,
      #00C28A 100%);
  color: white;
  overflow: hidden;
  padding: 2rem 1rem;
  box-sizing: border-box;
}
.practice-pays-badge {
  width: 100%;
  max-width: 600px;
  margin: 0 auto 1.5rem;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.15);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50px;
  padding: 0.75rem 1rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}
.badge-text {
  display: inline-block;
  white-space: nowrap;
  font-weight: 800;
  font-size: 1.1rem;
  color: #ffffff;
  text-shadow: 0 0 20px rgba(255, 215, 0, 0.8), 0 2px 4px rgba(0, 0, 0, 0.3);
  letter-spacing: 2px;
  text-transform: uppercase;
  padding: 0 2rem;
  animation: scrollText 20s linear infinite, shimmer 3s ease-in-out infinite;
}
@keyframes scrollText {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes shimmer {
  0%, 100% {
    text-shadow: 0 0 20px rgba(255, 215, 0, 0.8), 0 2px 4px rgba(0, 0, 0, 0.3);
  }
  50% {
    text-shadow:
      0 0 30px rgba(255, 215, 0, 1),
      0 0 40px rgba(255, 255, 255, 0.6),
      0 2px 4px rgba(0, 0, 0, 0.3);
  }
}
.badge-icon {
  color: #FFD700;
  font-size: 1.3rem;
  display: inline-block;
  margin: 0 0.3rem;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
  animation: pulse 2s ease-in-out infinite;
}
@media (max-width: 768px) {
  .hero-section {
    padding: 2.5rem 1.25rem 5rem;
    min-height: auto;
  }
  .practice-pays-badge {
    width: 85%;
    max-width: none;
    margin: 0 auto 1.5rem;
    padding: 0.625rem 0.5rem;
  }
  .badge-text {
    font-size: 0.875rem;
    letter-spacing: 1px;
    padding: 0 0.5rem;
  }
  .badge-icon {
    font-size: 1rem;
  }
  .hero-content {
    padding: 0;
    width: 100%;
    max-width: 100%;
  }
  .hero-text {
    padding: 0 1.25rem;
    box-sizing: border-box;
  }
  .solution-title {
    font-size: 1.75rem;
    line-height: 1.3;
    margin-bottom: 1rem;
    padding: 0;
  }
  .solution-text {
    font-size: 1rem;
    line-height: 1.6;
    max-width: 100%;
    margin: 0 0 2rem;
    padding: 0;
  }
  .hero-stats {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    margin: 0;
    padding: 0 1.25rem;
    width: 100%;
    box-sizing: border-box;
  }
  .stat-item {
    width: 100%;
    max-width: 400px;
    padding: 1.5rem 1.25rem;
    margin-bottom: 0;
  }
  .stat-number {
    font-size: 2.25rem;
  }
  .stat-label {
    font-size: 0.875rem;
    line-height: 1.4;
  }
  .hero-wave-svg {
    height: 110px;
  }
}
@media (max-width: 480px) {
  .hero-section {
    padding: 2rem 1rem 4.5rem;
  }
  .practice-pays-badge {
    width: 90%;
    padding: 0.5rem 0.25rem;
  }
  .badge-text {
    font-size: 0.75rem;
    letter-spacing: 0.5px;
    padding: 0 0.75rem;
  }
  .badge-icon {
    font-size: 0.875rem;
  }
  .hero-text {
    padding: 0 1rem;
  }
  .solution-title {
    font-size: 1.5rem;
    line-height: 1.3;
  }
  .solution-text {
    font-size: 0.9375rem;
    line-height: 1.6;
  }
  .hero-stats {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    padding: 0 1rem;
  }
  .stat-item {
    width: 100%;
    max-width: 350px;
    padding: 1.25rem 1rem;
  }
  .stat-number {
    font-size: 2rem;
  }
  .stat-label {
    font-size: 0.8125rem;
  }
  .hero-wave-svg {
    height: 90px;
  }
}
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}
@keyframes scrollText {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
.hero-wave {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px;
  line-height: 0;
  width: 100%;
  pointer-events: none;
  z-index: 0;
}
.hero-wave-svg {
  display: block;
  width: 100%;
  height: 180px;
  color: #ffffff;
}
.hero-content {
  position: relative;
  z-index: 2;
}
.hero-section {
  padding-bottom: 4rem;
}
.features-section,
.courses-section,
.demo-360-section,
.live-section,
.rooms-section,
.ai-coach-section,
.wallet-section {
  background: #ffffff;
  position: relative;
  z-index: 0;
}
.features-section + section,
.courses-section + section,
.demo-360-section + section,
.live-section + section,
.rooms-section + section,
.ai-coach-section + section,
.wallet-section + section {
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04) inset;
}
.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background:
    radial-gradient(
      circle at 20% 50%,
      rgba(255, 255, 255, 0.1) 0%,
      transparent 50%),
    radial-gradient(
      circle at 80% 80%,
      rgba(255, 255, 255, 0.1) 0%,
      transparent 50%);
  pointer-events: none;
}
.hero-content {
  position: relative;
  z-index: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  padding: 4rem 0;
}
.features-section {
  background: #ffffff;
  position: relative;
}
.hero-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin-top: 2.5rem;
}
.stat-item {
  text-align: center;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}
.stat-number {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
  display: block;
}
.stat-label {
  font-size: 0.9rem;
  opacity: 0.95;
  text-transform: uppercase;
  letter-spacing: 1px;
  display: block;
}
.hero-image {
  position: relative;
  height: 500px;
}
.floating-card {
  position: absolute;
  background: rgba(255, 255, 255, 0.15);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 16px;
  padding: 1.5rem;
  display: flex;
  align-items: center;
  gap: 1rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  animation: float 3s ease-in-out infinite;
}
.floating-card svg {
  flex-shrink: 0;
}
.floating-card span {
  font-weight: 600;
  font-size: 1.125rem;
}
.card-1 {
  top: 10%;
  left: 0;
  animation-delay: 0s;
}
.card-2 {
  top: 40%;
  right: 10%;
  animation-delay: 1s;
}
.card-3 {
  bottom: 15%;
  left: 15%;
  animation-delay: 2s;
}
@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  animation: bounce 2s infinite;
}
.scroll-indicator svg {
  width: 32px;
  height: 32px;
}
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateX(-50%) translateY(0);
  }
  40% {
    transform: translateX(-50%) translateY(-10px);
  }
  60% {
    transform: translateX(-50%) translateY(-5px);
  }
}
section {
  padding: 6rem 0;
  scroll-margin-top: 80px;
}
section + section {
  position: relative;
}
section + section:before {
  content: "";
  position: absolute;
  top: -1px;
  left: 50%;
  transform: translateX(-50%);
  width: 60%;
  height: 1px;
  background:
    linear-gradient(
      90deg,
      rgba(11, 110, 255, 0) 0%,
      rgba(11, 110, 255, .35) 10%,
      rgba(0, 194, 138, .45) 50%,
      rgba(11, 110, 255, .35) 90%,
      rgba(11, 110, 255, 0) 100%);
  filter: drop-shadow(0 2px 6px rgba(0, 0, 0, .4));
}
section + section:after {
  content: "";
  position: absolute;
  top: -24px;
  left: 50%;
  transform: translateX(-50%);
  width: 140px;
  height: 40px;
  pointer-events: none;
  background:
    radial-gradient(
      circle at 50% 60%,
      rgba(11, 110, 255, 0.25),
      rgba(0, 194, 138, 0.25),
      transparent 70%);
  opacity: .35;
}
.section-header {
  text-align: center;
  margin-bottom: 4rem;
  animation: fadeInUp 0.6s ease-out;
}
.section-header h2 {
  font-size: 2.75rem;
  font-weight: 800;
  margin-bottom: 1rem;
  line-height: 1.2;
}
.section-subtitle {
  font-size: 1.25rem;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}
.features-section {
  --layout-shadow-base: 0 4px 12px rgba(0, 0, 0, 0.08), 0 2px 4px rgba(0, 0, 0, 0.06);
  --color-border-default: #b8c5d0;
  --color-border-secondary: #e2e8f0;
  --layout-radius-base: 12px;
  background:
    linear-gradient(
      180deg,
      #f8fafc 0%,
      #ffffff 50%,
      #f1f5f9 100%);
  position: relative;
  overflow: hidden;
}
.features-section::before {
  content: "";
  position: absolute;
  top: -50%;
  right: -10%;
  width: 600px;
  height: 600px;
  background:
    radial-gradient(
      circle,
      rgba(99, 102, 241, 0.08) 0%,
      transparent 70%);
  border-radius: 50%;
  pointer-events: none;
}
.features-section::after {
  content: "";
  position: absolute;
  bottom: -30%;
  left: -5%;
  width: 500px;
  height: 500px;
  background:
    radial-gradient(
      circle,
      rgba(14, 165, 233, 0.06) 0%,
      transparent 70%);
  border-radius: 50%;
  pointer-events: none;
}
.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  position: relative;
  z-index: 1;
}
.feature-card {
  background: #ffffff;
  padding: 2.5rem;
  border-radius: var(--layout-radius-base);
  border: 2px solid var(--color-border-default);
  outline: 1px solid var(--color-border-secondary);
  outline-offset: -3px;
  box-shadow:
    var(--layout-shadow-base),
    0 1px 3px rgba(0, 0, 0, 0.05),
    inset 0 1px 0 rgba(255, 255, 255, 0.9),
    inset 0 -1px 0 rgba(0, 0, 0, 0.03);
  background-image:
    linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0.95),
      rgba(249, 250, 251, 0.98)),
    linear-gradient(
      135deg,
      rgba(255, 255, 255, 0.5) 0%,
      transparent 100%);
  background-image:
    linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0.95),
      rgba(249, 250, 251, 0.98)),
    linear-gradient(
      135deg,
      rgba(255, 255, 255, 0.5) 0%,
      transparent 100%);
  background-color: #ffffff;
  background-blend-mode: normal;
  background-blend-mode: normal;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  animation: fadeInUp 0.6s ease-out;
  animation-fill-mode: both;
  position: relative;
  overflow: hidden;
  border-image:
    linear-gradient(
      to bottom,
      rgba(99, 102, 241, 0.1),
      rgba(14, 165, 233, 0.08)) 1;
  border-image:
    linear-gradient(
      to bottom,
      rgba(99, 102, 241, 0.1),
      rgba(14, 165, 233, 0.08)) 1;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.feature-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background:
    linear-gradient(
      90deg,
      transparent,
      currentColor,
      transparent);
  opacity: 0;
  transition: opacity 0.4s ease;
}
.feature-card::after {
  content: "";
  position: absolute;
  inset: -1px;
  border-radius: var(--layout-radius-base);
  padding: 2px;
  background:
    linear-gradient(
      135deg,
      rgba(99, 102, 241, 0.08) 0%,
      rgba(14, 165, 233, 0.06) 50%,
      rgba(99, 102, 241, 0.08) 100%);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask-composite: exclude;
  pointer-events: none;
  opacity: 0.6;
  transition: opacity 0.4s ease;
}
.feature-card:nth-child(1) {
  animation-delay: 0.1s;
}
.feature-card:nth-child(2) {
  animation-delay: 0.2s;
}
.feature-card:nth-child(3) {
  animation-delay: 0.3s;
}
.feature-card:nth-child(4) {
  animation-delay: 0.4s;
}
.feature-card:nth-child(5) {
  animation-delay: 0.5s;
}
.feature-card:nth-child(6) {
  animation-delay: 0.6s;
}
.feature-card:nth-child(1) {
  color: #6366f1;
}
.feature-card:nth-child(1) .feature-icon {
  background:
    linear-gradient(
      135deg,
      #6366f1 0%,
      #8b5cf6 100%);
}
.feature-card:nth-child(1):hover {
  border-color: rgba(99, 102, 241, 0.5);
  box-shadow: 0 8px 24px rgba(99, 102, 241, 0.25), var(--layout-shadow-base);
}
.feature-card:nth-child(2) {
  color: #14b8a6;
}
.feature-card:nth-child(2) .feature-icon {
  background:
    linear-gradient(
      135deg,
      #14b8a6 0%,
      #06b6d4 100%);
}
.feature-card:nth-child(2):hover {
  border-color: rgba(20, 184, 166, 0.5);
  box-shadow: 0 8px 24px rgba(20, 184, 166, 0.25), var(--layout-shadow-base);
}
.feature-card:nth-child(3) {
  color: #f59e0b;
}
.feature-card:nth-child(3) .feature-icon {
  background:
    linear-gradient(
      135deg,
      #f59e0b 0%,
      #f97316 100%);
}
.feature-card:nth-child(3):hover {
  border-color: rgba(245, 158, 11, 0.5);
  box-shadow: 0 8px 24px rgba(245, 158, 11, 0.25), var(--layout-shadow-base);
}
.feature-card:nth-child(4) {
  color: #ec4899;
}
.feature-card:nth-child(4) .feature-icon {
  background:
    linear-gradient(
      135deg,
      #ec4899 0%,
      #f43f5e 100%);
}
.feature-card:nth-child(4):hover {
  border-color: rgba(236, 72, 153, 0.5);
  box-shadow: 0 8px 24px rgba(236, 72, 153, 0.25), var(--layout-shadow-base);
}
.feature-card:nth-child(5) {
  color: #10b981;
}
.feature-card:nth-child(5) .feature-icon {
  background:
    linear-gradient(
      135deg,
      #10b981 0%,
      #14b8a6 100%);
}
.feature-card:nth-child(5):hover {
  border-color: rgba(16, 185, 129, 0.5);
  box-shadow: 0 8px 24px rgba(16, 185, 129, 0.25), var(--layout-shadow-base);
}
.feature-card:nth-child(6) {
  color: #0ea5e9;
}
.feature-card:nth-child(6) .feature-icon {
  background:
    linear-gradient(
      135deg,
      #0ea5e9 0%,
      #3b82f6 100%);
}
.feature-card:nth-child(6):hover {
  border-color: rgba(14, 165, 233, 0.5);
  box-shadow: 0 8px 24px rgba(14, 165, 233, 0.25), var(--layout-shadow-base);
}
.feature-card:hover {
  transform: translateY(-8px) scale(1.02);
  outline-width: 2px;
  outline-color: currentColor;
  outline-offset: -4px;
}
.feature-card:hover::before {
  opacity: 1;
}
.feature-card:hover::after {
  opacity: 1;
}
.feature-icon {
  width: 84px;
  height: 84px;
  border-radius: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.75rem;
  box-shadow: 0 8px 24px -8px currentColor;
  transition: all 0.4s ease;
  position: relative;
}
.feature-card:hover .feature-icon {
  transform: scale(1.1) rotate(5deg);
  box-shadow: 0 12px 32px -6px currentColor;
}
.feature-icon svg {
  color: white;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
}
.feature-card h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: #0f172a;
  margin-bottom: 1rem;
  transition: color 0.3s ease;
}
.feature-card:hover h3 {
  color: currentColor;
}
.feature-card p {
  color: #475569;
  line-height: 1.75;
  font-size: 1.0625rem;
  flex-grow: 1;
}
.feature-card .btn {
  margin-top: 1.5rem;
}
.courses-section {
  --course-shadow-base: 0 6px 16px rgba(0, 0, 0, 0.1), 0 2px 6px rgba(0, 0, 0, 0.06);
  --course-border-default: #a8b5c7;
  --course-border-light: #dde3eb;
  --course-radius: 20px;
  background:
    linear-gradient(
      180deg,
      var(--bg) 0%,
      var(--surface) 100%);
}
.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2.5rem;
}
.course-card {
  position: relative;
  background:
    linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0.97),
      rgba(252, 253, 254, 0.98));
  border-radius: var(--course-radius);
  padding: 2rem;
  border: 2.5px solid var(--course-border-default);
  outline: 1.5px solid var(--course-border-light);
  outline-offset: -4px;
  box-shadow:
    var(--course-shadow-base),
    0 2px 8px rgba(0, 0, 0, 0.04),
    inset 0 1px 0 rgba(255, 255, 255, 0.95),
    inset 0 -2px 0 rgba(0, 0, 0, 0.04);
  transition: all 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
  overflow: hidden;
  background-image:
    linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0.97),
      rgba(252, 253, 254, 0.98)),
    linear-gradient(
      135deg,
      rgba(255, 255, 255, 0.6) 0%,
      transparent 100%);
}
.course-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 5px;
  background:
    linear-gradient(
      90deg,
      var(--brand-primary),
      var(--brand-accent));
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
  box-shadow: 0 2px 12px rgba(11, 110, 255, 0.4);
}
.course-card::after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: var(--course-radius);
  padding: 2.5px;
  background:
    linear-gradient(
      135deg,
      rgba(11, 110, 255, 0.12) 0%,
      rgba(0, 194, 138, 0.08) 50%,
      rgba(11, 110, 255, 0.12) 100%);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  opacity: 0;
  transition: opacity 0.4s ease;
  pointer-events: none;
}
.course-card:hover::before {
  transform: scaleX(1);
}
.course-card:hover::after {
  opacity: 1;
}
.course-card:hover {
  transform: translateY(-10px) scale(1.01);
  border-color: var(--brand-primary);
  outline-color: rgba(11, 110, 255, 0.3);
  outline-width: 2px;
  box-shadow:
    0 24px 68px rgba(0, 0, 0, 0.18),
    0 8px 20px rgba(11, 110, 255, 0.15),
    inset 0 1px 0 rgba(255, 255, 255, 1),
    inset 0 -2px 0 rgba(0, 0, 0, 0.05);
}
.course-badge {
  display: inline-block;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.badge-beginner {
  background: rgba(76, 175, 80, 0.1);
  color: #4CAF50;
