  box-shadow:
    0 6px 22px -4px rgba(0, 0, 0, .35),
    0 2px 8px -2px rgba(0, 0, 0, .25),
    inset 0 1px 0 rgba(255, 255, 255, 0.35);
  transition:
    transform .55s cubic-bezier(.34, 1.56, .64, 1),
    box-shadow .45s ease,
    border-color .4s ease,
    outline-color .4s ease;
  overflow: hidden;
}
.impact-metric:before {
  content: "";
  position: absolute;
  inset: 0;
  background:
    linear-gradient(
      140deg,
      rgba(11, 110, 255, 0.35),
      rgba(0, 194, 138, 0.35));
  opacity: 0;
  transition: opacity .5s ease;
  border-radius: var(--impact-radius);
}
.impact-metric:after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: var(--impact-radius);
  padding: 2px;
  background:
    linear-gradient(
      135deg,
      rgba(11, 110, 255, 0.5),
      rgba(0, 194, 138, 0.4),
      rgba(11, 110, 255, 0.5));
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  opacity: 0;
  transition: opacity .5s ease;
  pointer-events: none;
}
.impact-metric:hover {
  transform: translateY(-8px) scale(1.02);
